<template>
  <div id="shop-item">
    <div class="item-selector">
      <check-button @checkBtnClick="checkedChange" :is-checked="itemInfo.checked"></check-button>
      <!-- <check-button @click.native="checkedChange" :is-checked="itemInfo.checked" class="check-button"></check-button> -->
      <!-- 给vue组件绑定事件时候，必须加上native ，否则会认为监听的是来自Item组件自定义的事件
          等同于在子组件中：  子组件内部处理click事件然后向外发送click事件 -->
    </div>   
    <div class="item-img">
      <img :src="itemInfo.image" alt="商品图片">
    </div>
    <div class="item-info">
      <div class="item-title">{{itemInfo.title}}</div>
      <div class="item-desc">{{itemInfo.desc}}</div>
      <div class="info-bottom">
        <div class="item-price left">￥{{itemInfo.price}}</div>
        <div class="item-count right">x{{itemInfo.count}}</div>
      </div>
    </div>
  </div>
</template>

<script>
  import CheckButton from "components/content/checkButton/CheckButton"

  export default {
    name: "CartListItem",
    components:{
      CheckButton
    },
    props: {
      itemInfo: {
        type: Object,
        default() {
          return {};
        }
      },
    },
    methods:{
      checkedChange(){
        this.itemInfo.checked = !this.itemInfo.checked
      }
    }
  };
</script>

<style scoped>
  #shop-item{
    width: 100%;
    display: flex;
    font-size: 0;
    padding: 5px;
    border-bottom: 1px solid #ccc;
  }

  .item-selector{
    width: 20px;
    display: flex;
    justify-content: center; /*项目位于容器的中心*/
    align-items: center; /*元素位于容器的中心*/
  }

  .item-title, .item-desc{
    overflow: hidden;
    white-space: nowrap; /*文本不会换行，文本会在在同一行上继续，直到遇到 <br> 标签为止*/
    text-overflow: ellipsis;/*文本超出框长度，超出部分用省略号代替*/
  }

  .item-img{
    padding: 5px;
  }

  .item-img img{
    width: 80px;
    height: 100px;
    display: block;
    border-radius: 5px;
  }

  .item-info{
    font-size: 17px;
    color: #333;
    padding: 5px 10px;
    position: relative;
    overflow: hidden;
  }

  .item-info .item-desc{
    font-size: 14px;
    color: #666;
    margin-top: 15px;
  }

  .info-bottom{
    margin-top: 10px;
    position: absolute;
    bottom: 10px;
    left: 10px;
    right: 10px;
  }

  .info-bottom .item-price{
    color: orangered;
  }

</style>
